(() => {

  [
  
    /**
     * Colecao de operadores de comparacao e aritimeticos
     */
    ['add',       '+'],
    ['and',       '&&'],
    ['different', '!='],
    ['divide',    '/'],
    ['equal',     '=='],
    ['gt',        '>'],
    ['gte',       '>='],
    ['lt',        '<'],
    ['lte',       '<='],
    ['mod',       '%'],
    ['multiply',  '*'],
    ['or',        '||'],
    ['subtract',  '-']

  /**
   * Construtor de modulos com operadores da colecion acima, diminui a quantidade
   * de arquivos com modulos parecidos 
   */
  ].forEach(([name, operator]) => Rex(name, () => Function(...['a', 'b', `return a ${operator} b`])))

})();